const { defineProperties } = Object;
/**
 * @param {?} input
 * @return {?}
 */
export function getSafeClassName(input) {
    let /** @type {?} */ inputAsString = input.toString();
    const /** @type {?} */ match = /^symbol\((.*)\)$/i.exec(inputAsString);
    if (match !== null) {
        inputAsString = `SymbolAction_${match[1]}`; // `;
    }
    return inputAsString.replace(/\W/g, '_');
}
/**
 * @param {?} input
 * @return {?}
 */
export function generateClassAction(input) {
    const /** @type {?} */ actionClassName = getSafeClassName(input);
    const /** @type {?} */ actionClass = (new Function(`return function ${actionClassName}() { }`))(); // `;
    return actionClass;
}
/**
 * @param {?} classAction
 * @param {?} actionTypeName
 * @param {?} getMethod
 * @return {?}
 */
export function defineClassActionProperties(classAction, actionTypeName, getMethod) {
    const /** @type {?} */ isMethod = (action) => {
        return action.type === actionTypeName;
    };
    const /** @type {?} */ castMethod = (action) => {
        return action;
    };
    defineProperties(classAction, {
        /**
         * Defines a static property to get the "type" key
         * of this action
         */
        type: {
            writable: false,
            enumerable: true,
            configurable: false,
            value: actionTypeName,
        },
        /**
         * Defines a static method to check if a plain action
         * has the same type key as this defined action.
         */
        is: {
            writable: false,
            enumerable: false,
            configurable: false,
            value: isMethod,
        },
        /**
         * Returns the plain action passed as argument with proper typings
         */
        cast: {
            writable: false,
            enumerable: false,
            configurable: false,
            value: castMethod,
        },
        /**
         * Returns a plain action with a proper typed payload.
         */
        get: {
            writable: false,
            enumerable: false,
            configurable: false,
            value: getMethod,
        },
        /**
         * Returns a plain action with a proper typed payload.
         */
        strictGet: {
            writable: false,
            enumerable: false,
            configurable: false,
            value: getMethod,
        },
    });
}
